.content {
    min-height: 100%;
    background-size: cover;
    background-position: center;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.engagement-content {
    background-image: url("images/test-background.jpg");
}

.wedding-content {
    /* background-image: url("images/fall-background.jpg"); */
    background-attachment: fixed;
    transform: translate3d(0,0,0);
    -webkit-transform: translate3d(0,0,0);
}

.content-title {
    font-size: 2rem;
    /* font-weight: bold; */
    letter-spacing: 4px;
    margin-top: 30px;
    text-transform: uppercase;
    margin-bottom: 30px;
    flex: 0 auto;
    text-align: left;
}

.content-title.wedding-title {
    font-size: 3rem;
    text-shadow: 2px 2px rgba(0, 0, 0, 0.55);
    color: white;
}