.home-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    z-index: 10;
  }
  
  .center-wrapper {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  
  .center-wrapper .wedding {
    position: relative;
    z-index: 10;
    font-size: 2.5rem;
    color: white;
    letter-spacing: 12px;
    margin-bottom: 15px;
  }
  
  .home-wrapper .bride-groom-names-wrapper {
    flex: 0 0 200px;
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    background-color: rgba(255, 255, 255, 0.85);
    color: #4d4f53;
    border-radius: 100px;
    height: 200px;
    width: 200px;
    box-shadow: 0 3px 6px 3px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
    text-transform: uppercase;
    letter-spacing: 3px;
  }
  
  .home-wrapper .bride-groom-names-wrapper .and {
    font-size: 1rem;
  }